@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

* {
    font-family: 'Pretendard-Regular';
}

.all {
    width: 90%;
    height: 90%;
    background-color: #ebeff3;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 0px 0px 25px rgb(189, 189, 189);
    border: 1px solid rgb(199, 199, 199);
    transform: translate(-50%, -50%);
    overflow: hidden;
}

.topbarContainer {
    background-color: #f4f4f4;
    border-bottom: 1px solid #e6e6e6;
    position:relative;
    display: flex;
    height: 40px;
    align-items: center;
    position: sticky;
    /* z-index: 100; */
}

.topbarLeft {
    flex:3;
}

.circle1 {
    padding-left: 20px;
    position: relative;
    top:2px;
    color: rgb(255, 93, 75);
}

.circle2, .circle3 {
    padding-left: 5px;
    position: relative;
    top:2px;
}

.circle2 {
    color: lightgray;
}

.circle3 {
    color: rgb(0, 218, 0);
}

.topbarCenter {
    flex: 3.3;
    position: relative;
    top:1px;
    color: rgb(87, 87, 87);
}