@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

* {
    font-family: 'Pretendard-Regular';
}

.body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Canvas {
    width: 34%;
    height: 34%;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0px 0px 6px rgb(50, 50, 94, 0.11), 0 0px 3px rgb(0, 0, 0, 0.068);
    /* border: solid 1px rgba(95, 95, 95, 0.262); */
    margin-top: 110px;
    z-index: 100;
}

.controls {
    margin-top: 30px;
    z-index: 200;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.controls .controls_colors {
    display: flex;
}

.controls_range {
    margin-bottom: 10px;
}

.controls_colors .controls_color {
    width: 30px;
    height: 30px;
    /* border-radius: 55px; */
    cursor: pointer;
    box-shadow: 0px 0px 6px rgb(50, 50, 94, 0.11), 0 0px 3px rgb(0, 0, 0, 0.068);
}

.controls .controls_bt {
    margin-top: 20px;
}

.undoredo_bt button {
    all: unset;
    cursor: pointer;
    background-color: #fff;
    padding: 5px 0px;
    width: 25px;
    text-align: center;
    border-radius: 50px;
    box-shadow: 0px 0px 6px rgb(50, 50, 94, 0.11), 0 0px 3px rgb(0, 0, 0, 0.068);
    /* border: 2px solid rgb(0, 0, 0, 0.2); */
    color: rgb(0, 0, 0, 0.8);
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    margin: 3px;
}

.controls_bt button {
    all: unset;
    cursor: pointer;
    background-color: #fff;
    padding: 5px 0px;
    width: 80px;
    text-align: center;
    border-radius: 50px;
    box-shadow: 0px 0px 6px rgb(50, 50, 94, 0.11), 0 0px 3px rgb(0, 0, 0, 0.068);
    /* border: 2px solid rgb(0, 0, 0, 0.2); */
    color: rgb(0, 0, 0, 0.8);
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    margin: 3px;
}

.controls_bt button:active {
    transform: scale(0.98);
}